.app {
  @apply min-h-screen flex flex-col bg-gray-50;
}
.dark .app {
  @apply bg-gray-900;
}

.app__header {
  flex-shrink: 0;
  min-height: 4rem;
}

.app__main {
  @apply flex-1 w-full;
  min-height: calc(100vh - 4rem);
}

.app__footer {
  flex-shrink: 0;
  margin-top: auto;
}

.app--loading .app__main {
  @apply opacity-50 pointer-events-none;
}

.app__loading-overlay {
  @apply fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm;
}
.app__loading-overlay .loading-spinner {
  @apply w-12 h-12 border-4 border-primary-200 border-t-primary-600 rounded-full animate-spin;
}

@media (prefers-reduced-motion: reduce) {
  .loading-spinner {
    animation: none;
    @apply border-primary-600;
  }
}
.skip-link {
  @apply absolute -top-10 left-4 z-50 bg-primary-600 text-white px-4 py-2 rounded-md;
  @apply focus:top-4 transition-all duration-200;
}
.skip-link:focus {
  @apply outline-none ring-2 ring-white ring-offset-2 ring-offset-primary-600;
}

@media (max-width: 768px) {
  .app__main {
    min-height: calc(100vh - 3.5rem);
  }
}
